import React, { Component } from 'react';
import { disableBodyScroll, enableBodyScroll } from 'body-scroll-lock';
import ProjectModal from "./ProjectModal";
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

class Project extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isModalOpened: false
    };
  }

  openModal = () => {
    disableBodyScroll();
    this.setState({ isModalOpened: true });
  }
  closeModal = () => {
    enableBodyScroll();
    this.setState({ isModalOpened: false });
  }

  render() {
    let { projectName, title, imagePath, imageAlt } = this.props;
    return (
      <React.Fragment>
        <div className="column is-12-mobile is-half-tablet is-one-third-desktop">
          <figure className="image is-3by2" onClick={function () { void (0) }}><img className="project-thumb" src={imagePath} alt={imageAlt} />
            <figcaption>
              <h1 className="title is-size-5 is-size-4-widescreen" dangerouslySetInnerHTML={{ __html: title }}></h1>
              <button onClick={this.openModal} className="project__button view-project" target="_blank">
                <span>See Project</span>
                <span className="icon"><i className="angle-right"></i></span>
              </button>
            </figcaption>
            <div className="overlay"></div>
          </figure>
        </div>
        <ReactCSSTransitionGroup
          transitionName='fade'
          transitionLeave={true}
          transitionEnter={false}
          transitionLeaveTimeout={400}
        >
          {
            this.state.isModalOpened ?
              (
                <ProjectModal
                  key={1}
                  projectName={projectName}
                  closeModal={this.closeModal}
                />

              ) : null
          }
        </ReactCSSTransitionGroup>
      </React.Fragment>
    );
  }
}

export default Project;